#footer {
    margin-top: 10vmax;
    padding: 2vmax;
    background-color: #021b32;
    color: azure;
    display: flex;
    align-items: center;
} 

.leftFooter{
    width: 20%;
    display: flex;
    font-size: 1vmax;
    flex-direction: column;
    align-items: center;
}

.leftFooter > h4{
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
font-size: 1vmax;
}
.leftFooter >p {
    text-align: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1vmax;
}   

.leftFooter >img {
    width: 10vmax;
    margin: 1vmax;
    text-align: center;


}

.midFooter{
    width: 60%;
    text-align: center;
}

.midFooter > h1 {
font-size: 2vmax;
font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
color: crimson
}

.midFooter > p {
max-width: 60%;
margin: 1vmax auto;
font-size: 2vmax;

}
.rightFooter {
    width: 20%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.rightFooter > h4{
    font-family: 'Courier New', Courier, monospace;
    font-size: 1.4vmax;
    text-decoration: underline;
}

.rightFooter > a {
    text-decoration: none;
    font-size: 1.3vmax;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    color: azure;
    margin: 0.5vmax;

}
